import { Touch } from '@syncfusion/ej2-base';
import * as cls from '../base/css-constant';
/**
 * kanban touch module
 */
export class KanbanTouch {
    /**
     * Constructor for touch module
     */
    constructor(parent) {
        this.parent = parent;
        this.tabHold = false;
    }
    wireTouchEvents() {
        this.element = this.parent.element.querySelector('.' + cls.CONTENT_CLASS);
        this.touchObj = new Touch(this.element, { tapHold: this.tapHoldHandler.bind(this) });
    }
    tapHoldHandler(e) {
        this.tabHold = true;
    }
    destroy() {
        this.touchObj.destroy();
        this.tabHold = false;
        this.element = null;
    }
}
